* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

nav {
  background-color: cornflowerblue;
  display: flex;
  padding: 0 25px;
  justify-content: space-between;
  align-items: center;
}

nav ul {
  display: flex;
  list-style: none;
}

ul li {
  margin: 10px 0px;
}

li a {
  text-decoration: none;
  padding: 50px 20px;
  color: aliceblue;
}

li a:hover {
  background-color: aliceblue;
  color: cornflowerblue;
}

nav button {
  height: 25px;
  width: 100px;
}

.create-post-page .cpcont {
  text-align: center;
  margin-top: 50px;
}
.cpcont h1 {
  /* position: absolute; */
  color: aliceblue;
  width: 100%;
  background-color: cornflowerblue;
}

.cpcont .inpgrp {
  margin-top: 70px;
}

.inpgrp input {
  width: 40%;
  height: 40px;
  border: 1px solid cornflowerblue;
  border-radius: 25px;
  padding: 0 20px;
  font-size: 1rem;
}
.inpgrp h4 {
  font-size: 1.2rem;
  margin: 20px 0;
}

.inpgrp textarea {
  width: 40%;
  border: 1px solid cornflowerblue;
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 1rem;
}
.cpcont button {
  margin-top: 25px;
  padding: 10px 25px;
  background-color: cornflowerblue;
  border-radius: 25px;
  font-size: 1.2rem;
  color: aliceblue;
}

.loginpage {
  text-align: center;
}
.loginpage p {
  margin-top: 100px;
}
.login {
  margin-top: 50px;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.login img {
  width: 50px;
}
.login button {
  width: 100px;
  height: 30px;
  border: 1px solid cornflowerblue;
  background-color: aliceblue;
  border-radius: 25px;
}

.homepage {
  padding: 50px 100px;
}
.post {
  padding: 10px 20px;
  border: 1px solid cornflowerblue;
  margin: 10px 0;
  border-radius: 25px;
  box-shadow: 10px 10px 5px cornflowerblue;
}
.post .top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.post p {
  padding: 20px 10px;
}
.post button {
  background-color: red;
  padding: 10px 20px;
  border: none;
  border-radius: 25px;
  color: aliceblue;
}

@media (max-width : 800px) {
  .homepage {
    padding: 40px 10px;
  }
  li a{
    padding: 10px;
  }
  .inpgrp input {
    width: 90%;
  }
  .inpgrp textarea {
    width: 90%;
  }
}